<template>
  <el-container>
    <div class="box">
      <el-form ref="form" :model="form" label-position='top' :rules='rules'>
        <el-form-item label="">
          <el-input v-model="form.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop='oldPassword'>
          <el-input v-model="form.oldPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop='newPassword1'>
          <el-input v-model="form.newPassword1" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop='newPassword1'>
          <el-input v-model="form.newPassword2" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="warning" @click='toChangePassword'></el-button>
        </el-form-item>
      </el-form>      
    </div>
  </el-container>
</template>

<script>
export default {
  data: function () {
    return {
      form: {
        username: 'admin',
        oldPassword: '',
        newPassword1: '',
        newPassword2: ''
      },
      rules: {
        oldPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }],
        newPassword1: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }],
        newPassword2: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }]
      }
    }
  },
  methods: {
    action: function (val1, val2) {
        this.$message({
          message: val1,
          type: val2,
          center: true
        })
    },
    clearForm: function () {
      this.form = {
        username: 'admin',
        oldPassword: '',
        newPassword1: '',
        newPassword2: ''
      }
    },
    changePassword: async function () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          const data = await this.$http.put('/changePassword', this.form)
          console.log(data)
          if(data.data.changePasswordFlag) {
            this.action('', 'success')
            this.clearForm()
          } else {
            this.action('' ,'error')
          }
        } else {
          console.log('error submit!!')
          return false   
        }
      })
    },
    toChangePassword: async function () {
      const data = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      if(data === 'confirm') {
        console.log(this.form)
        this.changePassword()
      }
    }
  }
}
</script>

<style  lang="less" scoped>
.el-container {
  width: 100%;
  height: 100%;
  position: relative;
  .box {
    width: 300px;
    height: 500px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 30px;
  }  
}
</style>
