<template>
  <el-container>
    <el-card class="box-card">
    <el-table
      :data="blogs"
      style="width: 100%"
      border>
      <el-table-column
        type='index'
        label="#"
        align='center'>
      </el-table-column>
      <el-table-column
        label="ID"
        prop='id'
        align='center'
        width='50px'>
      </el-table-column>
      <el-table-column
        label=""
        prop='title'
        align='center'
        width='300px'>
      </el-table-column>
      <el-table-column
        label=""
        prop='original'
        align='center'
        width='50px'>
      </el-table-column>
      <el-table-column
        label=""
        prop='category_name'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='category_ename'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='url'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='date'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='commentsNum'
        align='center'
        width='50px'>
      </el-table-column>
      <el-table-column
        label=""
        prop='viewsNum'
        align='center'
        width='50px'>
      </el-table-column>
      <el-table-column
        label=""
        align='center'
        width='200px'>
        <template v-slot='scope'>
          <el-button  style='padding: 5px;' type="warning" @click="editBlog(scope.row.id)"></el-button>
          <el-button style='padding: 5px;' type="danger" @click='toDeleteBlog(scope.row.id)'></el-button>
          <el-button style='padding: 5px;' type="success" @click='blogCommentsManage(scope.row.id)'></el-button>       
        </template>
      </el-table-column>
    </el-table>
    </el-card>
  </el-container>
</template>

<script>
export default {
  data: function () {
    return {
      blogs: []
    }
  },
  created: function () {
    this.getBlogsAll()
  },
  methods: {
    getBlogsAll: async function () {
      const data = await this.$http.get('/blogsAll')
      console.log(data)
      this.blogs = data.data
    },
    editBlog: function (id) {
      this.$router.push('/editBlog/' + id)
    },
    deleteBlog: async function (id) {
      const data = await this.$http.delete('/deleteBlog/' + id)
      console.log(data)
      if (data.data.deleteBlogFlag) {
        this.getBlogsAll()
        this.$message({
          message: 'Blog',
          type: 'success',
          center: true
        })
      }
    },
    toDeleteBlog: async function (id) {
      const data = await this.$confirm('Blog, ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      if(data === 'confirm') this.deleteBlog(id)
    },
    blogCommentsManage: function (id) {
      this.$router.push('/blogCommentsManage/' + id)
    }
  }
}
</script>

<style  lang="less" scoped>
.el-container {
  width: 100%;
  .el-card {
    width: 100%;
  }
}
</style>
