<template>
  <el-container>
    <el-card class="box-card">
      <el-button type="primary" @click="showDiolog('add')"></el-button>
    <el-table
      :data="categoryList"
      style="width: 100%;margin-top: 20px;"
      border>
      <el-table-column
        type='index'
        label="#"
        align='center'>
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID"
        align='center'>
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="src"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="ename"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        align='center'
        width='150px'>
        <template v-slot='scope'>
          <el-button style='padding: 5px;'type="warning" @click="showDiolog('edit', scope.row.id)"></el-button>
          <el-button style='padding: 5px;' type="danger" @click='toDeleteCategory(scope.row.id)'></el-button>        
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :title="diologTitle"
      :visible.sync="dialogVisible"
      width="30%">
      <el-form :model="categoryData" class="demo-form-inline" label-width='80px' ref='form' :rules='rules'>
        <el-form-item label="ID">
          <el-input v-model="categoryData.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop='name'>
          <el-input v-model="categoryData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop='src'>
          <el-input v-model="categoryData.src"></el-input>
        </el-form-item>
        <el-form-item label="" prop='ename'>
          <el-input v-model="categoryData.ename"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </span>
    </el-dialog>
    </el-card>
  </el-container>
</template>

<script>
export default {
  data: function () {
    return {
      categoryList: [],
      dialogVisible: false,
      diologTitle: '',
      categoryData: {
        name: '',
        id: '',
        src: '',
        ename: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }],
        src: [
          { required: true, message: '', trigger: 'blur' }],
        ename: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }]
      }
    }
  },
  created: function () {
    this.getCategory()
  },
  methods: {
    getCategory: async function () {
      const data = await this.$http.get('/categoryList')
      this.categoryList = data.data
      console.log(this.categoryList)
    },
    getCategoryById: async function (id) {
      const data = await this.$http.get('/categoryById/' + id)
      this.categoryData = data.data[0]
      console.log(this.categoryData)
    },
    clearCategoryData: function () {
      this.categoryData = {
        name: '',
        id: '',
        src: '',
        ename: ''
      }
    },
    addCategory: async function () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          this.dialogVisible = false
          const data = await this.$http.post('/addCategory', this.categoryData)
          if(data.data.addCategoryFlag) {
            this.getCategory()
            this.$message({
              message: '',
              center: true,
              type: 'success'
            })
          }
          console.log(data)
        } else {
          console.log('error submit!!')
          return false          
        }
      })
    },
    editCategory: async function () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          this.dialogVisible = false
          const data = await this.$http.put('/editCategory', this.categoryData)
          if(data.data.editCategoryFlag) {
            this.getCategory()
            this.$message({
              message: '',
              center: true,
              type: 'success'
            })
          }
          console.log(data)
        } else {
          console.log('error submit!!')
          return false  
        }
      })
    },
    deleteCategory: async function (id) {
      const data = await this.$http.delete('/deleteCategory/' + id)
      console.log(data)
      if(data.data.deleteCategoryFlag) {
        this.getCategory()
        this.$message({
          message: '',
          center: true,
          type: 'success'
        })
      }
    },
    showDiolog: function (val,id) {
      this.clearCategoryData()
      if (val === 'add') {
        this.diologTitle = ''
      }
      if (val === 'edit') {
        this.diologTitle = ''
        this.getCategoryById(id)
      }
      this.dialogVisible = true
    },
    onSubmit: function () {
      if (this.diologTitle === '') {
        this.addCategory()
      }
      if (this.diologTitle === '') {
        this.editCategory()
      }
    },
    toDeleteCategory: async function (id) {
      const data = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      if(data === 'confirm') this.deleteCategory(id)
      console.log(id)
    }
  }
}
</script>

<style  lang="less" scoped>
.el-container {
  width: 100%;
  .el-card {
    width: 100%;
  }
}
</style>
