<template>
  <el-container>
    <el-card class="box-card">
    <el-table
      :data="blogCommentsData"
      style="width: 100%"
      border>
      <el-table-column
        type='index'
        label="#"
        align='center'>
      </el-table-column>
      <el-table-column
        label="ID"
        prop='commentId'
        align='center'
        width='50px'>
      </el-table-column>
      <el-table-column
        label=""
        prop='username'
        align='center'
        width='100px'>
      </el-table-column>
      <el-table-column
        label=""
        prop='head'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='url'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='content'
        align='center'>
      </el-table-column>
      <el-table-column
        label=""
        prop='date'
        align='center'
        width='100px'>
      </el-table-column>
      <el-table-column
        label=""
        align='center'
        width='150px'>
        <template v-slot='scope'>
          <el-button style='padding: 5px;' type="warning" @click="toeditBlogComment(scope.row.commentId)"></el-button>
          <el-button style='padding: 5px;' type="danger" @click='toDeleteBlogComment(scope.row.commentId)'></el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%">
      <el-form :model="singleBlogCommentData" class="demo-form-inline" label-width='80px' ref='form' :rules='rules'>
        <el-form-item label="ID">
          <el-input v-model="singleBlogCommentData.commentId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop='username'>
          <el-input v-model="singleBlogCommentData.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop='head'>
          <el-input v-model="singleBlogCommentData.head"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="singleBlogCommentData.url"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="singleBlogCommentData.content"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </span>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  props: ['id'],
  data: function () {
    return {
      blogCommentsData: [],
      dialogVisible: false,
      singleBlogCommentData: {},
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }],
        head: [
          { required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created: function () {
    this.getBlogCommentsData()
  },
  methods: {
    getBlogCommentsData: async function () {
      const data = await this.$http.get('/getBlogComments/' + this.id)
      console.log(data)
      this.blogCommentsData = data.data
    },
    getSingleBlogCommentDataById: async function (id) {
      const data = await this.$http.get('/getSingleBlogCommentDataById/' + id)
      this.singleBlogCommentData = data.data[0]
      console.log(data)
    },
    toeditBlogComment: function (id) {
      console.log(id)
      this.getSingleBlogCommentDataById(id)
      this.dialogVisible = true
    },
    editBlogComment: async function () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          this.dialogVisible = false
          const data = await this.$http.put('/editBlogComment', this.singleBlogCommentData)
          console.log(data)
          if(data.data.editBlogCommentFlag) {
            this.getBlogCommentsData()
            this.$message({
              message: '',
              type: 'success',
              center: true
            })
          }
        } else {
          console.log('error submit!!')
          return false          
        }
      })
    },
    deleteBlogComment: async function (id) {
      const data = await this.$http.delete('/deleteBlogComment/' + id)
      if (data.data.deleteBlogCommentFlag) {
        this.getBlogCommentsData()
        this.$message({
          message: '',
          type: 'success',
          center: true
        })
      }
    },
    toDeleteBlogComment: async function (id) {
      const data = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      if(data === 'confirm') this.deleteBlogComment(id)
    },
    onSubmit: function () {
      this.editBlogComment()
    }
  }
}
</script>

<style  lang="less" scoped>
.el-container {
  width: 100%;
  .el-card {
    width: 100%;
  }
}
</style>
